
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import { isScrollBottom } from '@/utils/comm.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
components: {
XHeader
},
data() {
return {
price: 0,
page: 2,
xl: 0,
price_px: 0,
status: 0,
goodsInfo: 1,
priceTitle: "价格",
statusTitle: "分类",
xlTitle: "销量",
shaixuanTitle: "筛选",
priceList: ['默认', '从高到低', '从低到高'],
statusList: ['全部', '男鞋', ' 女鞋', '童鞋'],
xlList: ['默认', '从高到低', ' 从低到高'],
customItem: "全部",
upLoading: true,
upLoadingComplete: false,
selecttype: 0,
inputShowed: true,
keyword: "",
imgheight: "",
satrtprice: "",
endprice: "",
isView: false,
isLi: false,
isFenlei: false,
n_statusTitle: "",
n_status: 0,
nvabarData: {
showCapsule: 1,
title: '全部商品',
},
height: 0,
}
},
created() {
this.imgheight = window.innerWidth / 2;
this.showList();
},
mounted() {
this.$nextTick(()=>{
this.height = this.$refs.vux_header.$el.clientHeight;
isScrollBottom(this.onReachBottom)
})
},
methods: {
async showList(){
let that = this;
let data = {
plat: 3,
page: 1,
type_price: that.price,
class_id: that.status,
sales: that.xl,
type: 0,
kw: that.keyword,
satrtprice: that.satrtprice,
endprice: that.endprice
}
const [err, res] = await api.homelist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
this.goodsInfo = res.data.list;
this.defimg = true;
}
},
async onReachBottom(){
if(this.upLoadingComplete)return;
this.upLoading = true;
let that = this;
let data = {
plat: 2,
page: that.page,
type_price: that.price,
class_id: that.status,
sales: that.xl,
type: 0,
kw: that.keyword,
satrtprice: that.satrtprice,
endprice: that.endprice
}
const [err, res] = await api.homelist(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if (res.data.list != "") {
var thisres = res;
that.goodsInfo = that.goodsInfo.concat(thisres.data.list);
that.upLoading = false;
that.page = that.page + 1;
} else {
that.upLoadingComplete = true;
that.upLoading = false;
}
}
},
bindSexChange(index){
var status;
var title = this.statusList[index];
if (index == "1") {
status = 1833;
} else if (index == "2") {
status = 1846;
} else if (index == "3") {
status = 1859;
}
else {
status = 0;
title = "分类"
}
this.statusTitle = title;
this.status = status;
this.page = 2;
this.isFenlei = false;
this.upLoadingComplete = false; 
this.upLoading = false;
this.showList();
},
bindFenlei(){
if(window.isClick)return;
window.isClick = true;
this.isFenlei = !this.isFenlei;
setTimeout(() => {
window.isClick = false;
}, 300);
},
bindPriceChange(){
var that = this;
var price;
if (that.price_px == 0) {
that.price_px = 3;
price = 3;
} else if (that.price_px == 3) {
that.price_px = 4;
price = 4;
} else if (that.price_px == 4) {
that.price_px = 3;
price = 3;
}
this.price_px = that.price_px;
this.page = 2;
this.xl = price;
this.selecttype = 0;
this.upLoadingComplete =  false;  
this.upLoading = false;
this.showList();
},
bindxlChange(){
this.page = 2;
this.xl = 1;
this.upLoadingComplete = false;
this.upLoading = false;  
this.showList(this);
},
openShaixuan(){
if(window.isClick)return;
window.isClick = true;
this.isView = !this.isView;
setTimeout(() => {
window.isClick = false
}, 300);
},
openGoods(goodsid){
this.$router.push({
path: '/index/goodsInfoPindan',
query: {
goodsId: goodsid
}
})
},
checkShaixuanFenlei(index, title){
var status;
if (index == 1) {
status = 1833;
} else if (index == 2) {
status = 1846;
} else if (index == 3) {
status = 1859;
}
this.n_statusTitle = title;
this.n_status = status;
},
clearInput(){
this.keyword = "";
this.page = 2;
this.xl = 0;
this.price_px = 0;
this.statusTitle = "分类";
this.n_statusTitle = "分类";
this.status = 0;
this.satrtprice = "";
this.endprice = "";
this.upLoadingComplete = false;
this.showList()
},
starPrice(e){
this.starPrice = e.target.value;
},
endPrice(e){
this.endPrice = e.target.value;
},
Gosearch(){
this.page = 2;
this.upLoadingComplete = false;
if(this.n_statusTitle)this.statusTitle = this.n_statusTitle;
this.status = this.n_status;
this.isView = false;
this.showList();
},
showInput(){
this.inputShowed = true;
},
pailie(){
this.isLi = !this.isLi;
},
hideInput(){
this.inputShowed = false;
this.$refs.search.focus();
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



